import React, { Component } from 'react';
import LengthItems from './LengthItems';

class Length extends Component {
  state={
    unitType: "",
    value: "",
    mm: null,
    cm: null,
    dm: null,
    nm: null,
    µm: null,
    ft: null,
    in: null,
    mi: null,
    mil: null,
    m: null,
    km: null,
    yard: null,
  }

  onInputChange = (e) => {
    let value = (+e.target.value)
    if(value === 0){
      this.setState({
        value: null
      })
    }else{
    this.setState({
      value
    })}
  }

  onUnitChange = (e) => {
    this.setState({
      unitType: e.target.value
    })
  }

  onConversion = () => {
    let value= (this.state.value === null ? 0 : this.state.value)
    let unitType = this.state.unitType
    
    
    if(unitType === "MM"){
      this.setState({
        mm: value,
        cm: this.state.value / 10,
        m: this.state.value / 1000,
        km: this.state.value / 1000000,
        yard: (this.state.value / 914.4),
        nm: this.state.value * 1000000,
        dm: this.state.value / 100,
        ft: (this.state.value / 304.8),
        in: (this.state.value / 25.4),
        µm: this.state.value * 1000,
        mi: (this.state.value / 0.0000006213),
        mil: this.state.value * 39.37
      })
    }else if(unitType === "CM"){
      this.setState({
        mm: this.state.value * 10,
        cm: value,
        m: this.state.value / 100,
        km: this.state.value / 100000,
        yard: (this.state.value / 91.44),
        nm: this.state.value * 10000000,
        dm: this.state.value / 10,
        ft: (this.state.value / 30.48),
        in: (this.state.value / 2.54),
        µm: this.state.value * 10000,
        mi: (this.state.value / 160934.4),
        mil: this.state.value * 393.701
      })
    }else if(unitType === "M"){
      this.setState({
        mm: this.state.value * 1000000,
        cm: this.state.value * 100000,
        m: value,
        km: this.state.value / 1000,
        yard: (this.state.value * 1.094),
        nm: this.state.value * 100000000,
        dm: this.state.value * 10,
        ft: (this.state.value * 3.281),
        in: (this.state.value * 39.37),
        µm: this.state.value * 1000000,
        mi: (this.state.value / 1609.344),
        mil: this.state.value * 39370.079
      })
    }else if(unitType === "KM"){
      this.setState({
        mm: this.state.value * 1000,
        cm: this.state.value * 100,
        m: this.state.value * 1000,
        km: value,
        yard: (this.state.value * 1093.613),
        nm: this.state.value * 1000000000000,
        dm: this.state.value * 10000,
        ft: (this.state.value * 3280.84),
        in: (this.state.value * 1093.613),
        µm: this.state.value * 1000000000,
        mi: (this.state.value / 1.609),
        mil: this.state.value * 39370078.74
      })
    }else if(unitType === "Yard"){
      this.setState({
        mm: (value * 914.4),
        cm: (value * 91.44),
        m: (value / 1.094),
        km: (value / 1093.613),
        yard: value,
        nm: this.state.value * 914400000,
        dm: this.state.value * 9.144,
        ft: (this.state.value * 3),
        in: (this.state.value * 36),
        µm: this.state.value * 914400,
        mi: (this.state.value / 1760),
        mil: this.state.value * 36000
      })
    }else if(unitType === "DM"){
      this.setState({
        mm: (value * 100),
        cm: value * 10,
        m: (value / 10),
        km: (value / 10000),
        yard: (value / 9.144),
        nm: value * 100000000,
        dm: value,
        ft: (value / 3.048),
        in: (value * 3.937),
        µm: value * 100000,
        mi: (this.state.value / 16093.44),
        mil: this.state.value * 3937.008
      })
    }else if(unitType === "NM"){
      this.setState({
        mm: (value / 1000000),
        cm: (value / 10000000),
        m: (value / 1000000000),
        km: (value / 1000000000000),
        yard: (value / 0.00000009144),
        nm: value,
        dm: (value / 100000000),
        ft: (value / 0.0000000032),
        in: (value / 0.000000254),
        µm: (value / 1000),
        mi: (value / 0.000000000001609),
        mil: (value / 25400)
      })
    }else if(unitType === "µM"){
      this.setState({
        mm: (value / 10000),
        cm: (value / 10000),
        m: (value / 1000000),
        km: (value / 1000000000),
        yard: (value / 914400),
        nm: value * 1000,
        dm: (value / 100000),
        ft: (value / 304800),
        in: (value / 25400),
        µm: value,
        mi: (value / 0.000000001609),
        mil: (value / 25.4)
      })
    }else if(unitType === "Ft"){
      this.setState({
        mm: (value * 304.8),
        cm: (value * 30.48),
        m: (value / 3.281),
        km: (value / 3280.84),
        yard: (value / 3),
        nm: value * 304800000,
        dm: (value * 3.048),
        ft: (value),
        in: (value * 12),
        µm: value * 304800,
        mi: (value / 5280),
        mil: (value * 12000)
      })
    }else if(unitType === "In"){
      this.setState({
        mm: (value * 25.4),
        cm: (value * 2.54),
        m: (value / 39.37),
        km: (value / 39370.079),
        yard: (value / 36),
        nm: value * 25400000,
        dm: (value / 3.937),
        ft: (value / 12),
        in: (value),
        µm: value * 25400,
        mi: (value / 63360),
        mil: (value / 1000)
      })
    }else if(unitType === "Mi"){
      this.setState({
        mm: (value * 1609000),
        cm: (value * 160934.4),
        m: (value * 1609.344),
        km: (value * 1.609344),
        yard: (value * 1760),
        nm: value * 1609000000000,
        dm: (value * 16093.44),
        ft: (value * 5280),
        in: (value * 63360),
        µm: value * 1609000000,
        mi: (value),
        mil: (value * 1760)
      })
    }else if(unitType === "Mil"){
      this.setState({
        mm: (value / 39.37),
        cm: (value / 393.701),
        m: (value / 39370.079),
        km: (value / 39370000),
        yard: (value / 36000),
        nm: value * 304800000,
        dm: (value * 25400),
        ft: (value / 12000),
        in: (value / 1000),
        µm: value * 25.4,
        mi: (value / 63360000),
        mil: (value)
      })
    }
  }
  
  render(){
    return(
      <div>
        <div className="wrap_input">
        <input 
          className= "input"
          type= "number"
          value= {this.state.value}
          placeholder= "Enter Your Value"
          onChange= {this.onInputChange}
        />
        <LengthItems 
          onUnitChange= {this.onUnitChange}
        />
        </div>
         <button 
          onClick= {this.onConversion}
          className= "btn_convert"  
        >Convert
        </button>
        <div className="display">
        <p className= "display_unit">Milimeter : {this.state.mm}</p>
        <p className= "display_unit">Centimeter : {this.state.cm}</p>
        <p className= "display_unit">Meter : {this.state.m}</p>
        <p className= "display_unit">Kilometer : {this.state.km}</p>
        <p className= "display_unit">Yard : {this.state.yard}</p>
        <p className= "display_unit">Nanometer : {this.state.nm}</p>
        <p className= "display_unit">Micrometer : {this.state.µm}</p>
        <p className= "display_unit">Decimeter : {this.state.dm}</p>
        <p className= "display_unit">Feet : {this.state.ft}</p>
        <p className= "display_unit">Inch : {this.state.in}</p>
        <p className= "display_unit">Mile : {this.state.mi}</p>
        <p className= "display_unit">Mil : {this.state.mil}</p>
        </div>
      </div>
    )
  }
}

export default Length;
