import React from "react";

import DataSizeItems from "./DataSizeItems"

class DataSize extends React.Component{
    state={
        value: "",
        byte: null,
        bit: null,
        kb: null,
        mb: null,
        gb: null,
        tb: null,
        pb: null,
        nibbles: null
    }

    onInputChange = (e) => {
        let value = (+e.target.value)
        if(value === 0){
          this.setState({
            value: null
          })
        }else{
        this.setState({
          value
        })}
      }
    
      onUnitChange = (e) => {
        this.setState({
          unitType: e.target.value
        })
      }

      onConversion = () => {
        let value= (this.state.value === null ? 0 : this.state.value)
        let unitType = this.state.unitType

        if(unitType === "Byte"){
            this.setState({
                byte: value,
                bit: value * 8,
                kb: value / 1024,
                mb: value / 1048576,
                gb: value / 1073741824,
                tb: value / 1099511531398.98,
                pb: value / 1125899755262058.9,
                nibbles: value * 2 
            })
        }else if(unitType === "Bit"){
            this.setState({
                byte: value / 8,
                bit: value,
                kb: value / 8192,
                mb: value / 8388608,
                gb: value / 8589934592,
                tb: value / 8796092251191.81,
                pb: value / 9007198042096471,
                nibbles: value / 4 
            })
        }else if(unitType === "Kb"){
            this.setState({
                byte: value * 1000,
                bit: value * 8000,
                kb: value,
                mb: value / 1024,
                gb: value / 1048576,
                tb: value / 1073741729.88,
                pb: value / 1099511479748.1,
                nibbles: value * 2000 
            })
        }else if(unitType === "Mb"){
            this.setState({
                byte: value * 1048576,
                bit: value * 8388608,
                kb: value * 1024,
                mb: value,
                gb: value / 1024,
                tb: value / 1048575.91,
                pb: value / 1073741679.44,
                nibbles: value * 2097152 
            })
        }else if(unitType === "Gb"){
            this.setState({
                byte: value * 1073741824,
                bit: value * 8589934592,
                kb: value * 1048576,
                mb: value * 1024,
                gb: value,
                tb: value / 1024,
                pb: value / 1048575.86,
                nibbles: value * 2147483648 
            })
        }else if(unitType === "Tb"){
            this.setState({
                byte: value * 1099511531398.98,
                bit: value * 8796092251191.81,
                kb: value * 1073741729.88,
                mb: value * 1048575.91,
                gb: value * 1024,
                tb: value,
                pb: value / 1048575.86,
                nibbles: value * 2199023062797.95 
            })
        }else if(unitType === "Pb"){
            this.setState({
                byte: value * 1125899755262058.9,
                bit: value * 9007198042096471,
                kb: value * 1099511479748.1,
                mb: value * 1073741679.44,
                gb: value * 1048575.86,
                tb: value * 1024,
                pb: value,
                nibbles: value * 2251799510524117.8 
            })
        }else if(unitType === "Nib"){
            this.setState({
                byte: value / 2,
                bit: value * 4,
                kb: value / 2048,
                mb: value / 2097152,
                gb: value / 2147483648,
                tb: value / 2199023062797.95,
                pb: value / 2251799510524117.8,
                nibbles: value 
            })
        }
      }

    render(){
        return(
            <div>
                <div className="wrap_input">
                <input 
                className= "input"
                type= "number"
                value= {this.state.value}
                placeholder= "Enter Your Value"
                onChange= {this.onInputChange}
                />
                <DataSizeItems 
                onUnitChange= {this.onUnitChange}
                />
                </div>
                <button 
                onClick= {this.onConversion}
                className= "btn_convert"  
                >Convert
                </button>
                <div className="display">
                <p className= "display_unit">Bit : {this.state.bit}</p>
                <p className= "display_unit">Nibbles : {this.state.nibbles}</p>
                <p className= "display_unit">Byte : {this.state.byte}</p>
                <p className= "display_unit">Kilobyte : {this.state.kb}</p>
                <p className= "display_unit">Megabyte : {this.state.mb}</p>
                <p className= "display_unit">Gigabyte : {this.state.gb}</p>
                <p className= "display_unit">Terabyte : {this.state.tb}</p>
                <p className= "display_unit">Petabyte : {this.state.pb}</p>
                </div>
            </div>
        )
    }
}

export default DataSize;