import React from "react";


import TimeItems from "./TImeItems";

class Time extends React.Component{
    state={
        value: "",
        ms: null,
        ns: null,
        µs: null,
        sec: null,
        min: null,
        hr: null,
        day: null,
        week: null
    }

    onInputChange = (e) => {
        let value = (+e.target.value)
        if(value === 0){
          this.setState({
            value: null
          })
        }else{
        this.setState({
          value
        })}
      }
    
      onUnitChange = (e) => {
        this.setState({
          unitType: e.target.value
        })
      }

      onConversion = () => {
        let value= (this.state.value === null ? 0 : this.state.value)
        let unitType = this.state.unitType

        if(unitType === "MS"){
            this.setState({
                ms: value,
                ns: value * 1000000,
                µs: value * 1000,
                sec: value / 1000,
                min: value / 60000,
                hr: value / 3600000,
                day: value / 86400000,
                week: value / 604800000
            })
        }else if(unitType === "NS"){
            this.setState({
                ms: value / 1000000,
                ns: value,
                µs: value / 1000,
                sec: value / 1000000000,
                min: value / 60000000000,
                hr: value / 3600000000000,
                day: value / 864000000000000,
                week: value / 604800000000000
            })
        }else if(unitType === "µS"){
            this.setState({
                ms: value / 1000,
                ns: value * 1000,
                µs: value,
                sec: value / 1000000,
                min: value / 60000000,
                hr: value / 3600000000,
                day: value / 86400000000,
                week: value / 604800000000
            })
        }else if(unitType === "Sec"){
            this.setState({
                ms: value * 1000,
                ns: value * 100000000,
                µs: value * 1000000,
                sec: value,
                min: value / 60,
                hr: value / 3600,
                day: value / 86400,
                week: value / 604800
            })
        }else if(unitType === "Min"){
            this.setState({
                ms: value * 60000,
                ns: value * 60000000000,
                µs: value * 60000000,
                sec: value * 60,
                min: value,
                hr: value / 60,
                day: value / 1440,
                week: value / 10080
            })
        }else if(unitType === "Hr"){
            this.setState({
                ms: value * 3600000,
                ns: value * 3600000000000,
                µs: value * 3600000000,
                sec: value * 3600,
                min: value * 60,
                hr: value,
                day: value / 24,
                week: value / 168
            })
        }else if(unitType === "Day"){
            this.setState({
                ms: value * 86400000,
                ns: value * 864000000000000,
                µs: value * 86400000000,
                sec: value * 86400,
                min: value * 1440,
                hr: value * 24,
                day: value,
                week: value / 7
            })
        }else if(unitType === "Week"){
            this.setState({
                ms: value * 604800000,
                ns: value * 604800000000000,
                µs: value * 604800000000,
                sec: value * 604800,
                min: value * 10080,
                hr: value * 168,
                day: value * 7,
                week: value
            })
        }
    }

    render(){
        return(
            <div>
                <div className="wrap_input">
                <input 
                className= "input"
                type= "number"
                value= {this.state.value}
                placeholder= "Enter Your Value"
                onChange= {this.onInputChange}
                />
                <TimeItems 
                onUnitChange= {this.onUnitChange}
                />
                </div>
                <button 
                onClick= {this.onConversion}
                className= "btn_convert"  
                >Convert
                </button>
                <div className="display">
                <p className= "display_unit">Milisecond : {this.state.ms}</p>
                <p className= "display_unit">Nanosecond : {this.state.ns}</p>
                <p className= "display_unit">Microsecond : {this.state.µs}</p>
                <p className= "display_unit">Second : {this.state.sec}</p>
                <p className= "display_unit">Minute : {this.state.min}</p>
                <p className= "display_unit">Hour : {this.state.hr}</p>
                <p className= "display_unit">Day : {this.state.day}</p>
                <p className= "display_unit">Week : {this.state.week}</p>
                </div>
            </div>
        )
    }
}

export default Time;