import React from "react";

import SpeedItems from "./SpeedItems"

class Speed extends React.Component{
    state={
        value: "",
        mmps: null,
        µmps: null,
        mps: null,
        mph: null,
        kmps: null,
        kmph: null,
        FPS: null,
        knots: null
    }

    onInputChange = (e) => {
        let value = (+e.target.value)
        if(value === 0){
          this.setState({
            value: null
          })
        }else{
        this.setState({
          value
        })}
      }
    
      onUnitChange = (e) => {
        this.setState({
          unitType: e.target.value
        })
      }

      onConversion = () => {
        let value= (this.state.value === null ? 0 : this.state.value)
        let unitType = this.state.unitType

        if(unitType === "mmps"){
            this.setState({
                mmps: value,
                µmps: value * 1000,
                mps: value / 1000,
                mph: value / 447.04,
                kmps: value / 1000000,
                kmph: value / 277.778,
                FPS: value / 304.8,
                knots: value / 514.444
            })
        }else if(unitType === "µmps"){
            this.setState({
                mmps: value / 1000,
                µmps: value,
                mps: value / 1000000,
                mph: value / 447040,
                kmps: value / 1000000000,
                kmph: value / 277777.778,
                FPS: value / 304800,
                knots: value / 514444.444
            })
        }else if(unitType === "mps"){
            this.setState({
                mmps: value * 1000,
                µmps: value * 1000000,
                mps: value,
                mph: value * 2.237,
                kmps: value / 1000,
                kmph: value * 3.6,
                FPS: value * 3.281,
                knots: value / 1.944
            })
        }else if(unitType === "mph"){
            this.setState({
                mmps: value * 447.04,
                µmps: value * 447040,
                mps: value / 2.237,
                mph: value,
                kmps: value / 2236.936,
                kmph: value * 1.609,
                FPS: value * 1.467,
                knots: value / 1.151
            })
        }else if(unitType === "kmps"){
            this.setState({
                mmps: value * 1000000,
                µmps: value * 1000000000,
                mps: value * 1000,
                mph: value * 2236.936,
                kmps: value,
                kmph: value * 3600,
                FPS: value * 3280.84,
                knots: value / 1943.844
            })
        }else if(unitType === "kmph"){
            this.setState({
                mmps: value * 277.778,
                µmps: value * 277777.778,
                mps: value / 3.6,
                mph: value / 1.609,
                kmps: value * 3600,
                kmph: value,
                FPS: value / 1.097,
                knots: value / 1.852
            })
        }else if(unitType === "FPS"){
            this.setState({
                mmps: value * 304.8,
                µmps: value * 304800,
                mps: value / 3.281,
                mph: value / 1.467,
                kmps: value / 3280.84,
                kmph: value * 1.097,
                FPS: value,
                knots: value / 1.688
            })
        }else if(unitType === "Knots"){
            this.setState({
                mmps: value * 514.444,
                µmps: value * 514444.444,
                mps: value / 1.944,
                mph: value * 1.151,
                kmps: value / 1943.844,
                kmph: value * 1.852,
                FPS: value * 1.688,
                knots: value
            })
        }
      }

    render(){
        return(
            <div>
                <div className="wrap_input">
                <input 
                className= "input"
                type= "number"
                value= {this.state.value}
                placeholder= "Enter Your Value"
                onChange= {this.onInputChange}
                />
                <SpeedItems 
                onUnitChange= {this.onUnitChange}
                />
                </div>
                <button 
                onClick= {this.onConversion}
                className= "btn_convert"  
                >Convert
                </button>
                <div className="display">
                <p className= "display_unit">Milimeter/Sec : {this.state.mmps}</p>
                <p className= "display_unit">Micrometer/Sec : {this.state.µmps}</p>
                <p className= "display_unit">Meter/Sec : {this.state.mps}</p>
                <p className= "display_unit">Kilometer/Sec : {this.state.kmps}</p>
                <p className= "display_unit">Miles/Hr : {this.state.mph}</p>
                <p className= "display_unit">Kilometer/Hr : {this.state.kmph}</p>
                <p className= "display_unit">Feet/Sec : {this.state.FPS}</p>
                <p className= "display_unit">Knots : {this.state.knots}</p>
                </div>
            </div>
        )
    }
}

export default Speed;