import React from "react";

import AreaItems from "./AreaItems";

class Area extends React.Component{
    state={
        value: "",
        cm2: null,
        dm2: null,
        m2: null,
        km2: null,
        ft2: null,
        in2: null,
        yard2: null,
        mi2: null,
        ares: null,
        acres: null,
        hectares: null
    }

    onInputChange = (e) => {
        let value = (+e.target.value)
        if(value === 0){
          this.setState({
            value: null
          })
        }else{
        this.setState({
          value
        })}
      }
    
      onUnitChange = (e) => {
        this.setState({
          unitType: e.target.value
        })
      }
      
      onConversion = () => {
        let value= (this.state.value === null ? 0 : this.state.value)
        let unitType = this.state.unitType

        if(unitType === "dm2"){
            this.setState({
                dm2: value,
                cm2: value * 100,
                m2: value / 100,
                km2: value / 100000000,
                ft2: value / 9.29,
                in2: value * 15.5,
                yard2: value / 83.613,
                mi2: value / 259000000,
                ares: value / 10000,
                acres: value / 404685.642,
                hectares: value / 1000000
            })
        }else if(unitType === "cm2"){
            this.setState({
                dm2: value / 100,
                cm2: value,
                m2: value / 10000,
                km2: value / 10000000000,
                ft2: value / 929.03,
                in2: value / 6.452,
                yard2: value / 8361.274,
                mi2: value / 25900000000,
                ares: value / 1000000,
                acres: value / 40470000,
                hectares: value / 100000000
            })
        }else if(unitType === "m2"){
            this.setState({
                dm2: value * 100,
                cm2: value * 10000,
                m2: value,
                km2: value / 1000000,
                ft2: value * 10.764,
                in2: value * 1550.003,
                yard2: value * 1.196,
                mi2: value / 2590000,
                ares: value / 100,
                acres: value / 4046.856,
                hectares: value / 10000
            })
        }else if(unitType === "km2"){
            this.setState({
                dm2: value * 100000000,
                cm2: value * 10000000000,
                m2: value * 1000000,
                km2: value,
                ft2: value * 10760000,
                in2: value * 1550000000,
                yard2: value * 1196000,
                mi2: value / 2.59,
                ares: value * 10000,
                acres: value * 247.105,
                hectares: value * 100
            })
        }else if(unitType === "ft2"){
            this.setState({
                dm2: value * 9.29,
                cm2: value * 929.03,
                m2: value / 10.764,
                km2: value / 10760000,
                ft2: value,
                in2: value * 144,
                yard2: value / 9,
                mi2: value / 27880000,
                ares: value / 1076.391,
                acres: value / 43560,
                hectares: value / 107639.104
            })
        }else if(unitType === "in2"){
            this.setState({
                dm2: value / 15.5,
                cm2: value * 6.452,
                m2: value / 1550.003,
                km2: value / 1550000000,
                ft2: value / 144,
                in2: value,
                yard2: value / 1296,
                mi2: value / 4014000000,
                ares: value / 155000.31,
                acres: value / 6273000,
                hectares: value / 15500000
            })
        }else if(unitType === "yard2"){
            this.setState({
                dm2: value / 83.6127,
                cm2: value / 8361.27,
                m2: value / 1.196,
                km2: value / 1196000,
                ft2: value * 9,
                in2: value * 1296,
                yard2: value,
                mi2: value / 3098000,
                ares: value / 119.599,
                acres: value / 4840,
                hectares: value / 11959.9
            })
        }else if(unitType === "mi2"){
            this.setState({
                dm2: value * 259000000,
                cm2: value * 2590000000000,
                m2: value * 2590000,
                km2: value * 2.59,
                ft2: value * 27880000,
                in2: value * 4014000000,
                yard2: value * 3098000,
                mi2: value,
                ares: value * 25899.881,
                acres: value * 640,
                hectares: value * 258.999
            })
        }else if(unitType === "ares"){
            this.setState({
                dm2: value * 1000,
                cm2: value * 1000000,
                m2: value * 100,
                km2: value * 10000,
                ft2: value * 1076.391,
                in2: value * 155000.31,
                yard2: value * 119.599,
                mi2: value * 25899.881,
                ares: value,
                acres: value / 40.469,
                hectares: value / 100
            })
        }else if(unitType === "acres"){
            this.setState({
                dm2: value * 404685.642,
                cm2: value * 40470000,
                m2: value * 4046.856,
                km2: value / 247.105,
                ft2: value * 43560,
                in2: value * 6273000,
                yard2: value * 4840,
                mi2: value / 640,
                ares: value * 40.469,
                acres: value ,
                hectares: value / 2.471
            })
        }else if(unitType === "acres"){
            this.setState({
                dm2: value * 1000000,
                cm2: value * 100000000,
                m2: value * 10000,
                km2: value / 100,
                ft2: value * 107639.104,
                in2: value * 15500000,
                yard2: value * 11959.9,
                mi2: value / 258.999,
                ares: value * 100,
                acres: value * 2.47105 ,
                hectares: value
            })
        }
      }

    render(){
        return(
          <div>
            <div className="wrap_input">
            <input 
              className= "input"
              type= "number"
              value= {this.state.value}
              placeholder= "Enter Your Value"
              onChange= {this.onInputChange}
            />
            <AreaItems 
              onUnitChange= {this.onUnitChange}
            />
            </div>
             <button 
              onClick= {this.onConversion}
              className= "btn_convert"  
            >Convert
            </button>
            <div className="display">
            <p className= "display_unit">dm² : {this.state.dm2}</p>
            <p className= "display_unit">cm² : {this.state.cm2}</p>
            <p className= "display_unit">m² : {this.state.m2}</p>
            <p className= "display_unit">km² : {this.state.km2}</p>
            <p className= "display_unit">ft² : {this.state.ft2}</p>
            <p className= "display_unit">in² : {this.state.in2}</p>
            <p className= "display_unit">yard² : {this.state.yard2}</p>
            <p className= "display_unit">mi² : {this.state.mi2}</p>
            <p className= "display_unit">Ares : {this.state.ares}</p>
            <p className= "display_unit">Acres : {this.state.acres}</p>
            <p className= "display_unit">Hectares : {this.state.hectares}</p>
            </div>
          </div>
        )
      }
}

export default Area;