import React from "react";
import SideMenu from "./SideMenu";

class Menu extends React.Component{
    state={
        menuDisplay: false
    }

    onClickMenu = () => {
        if(this.state.menuDisplay === false){
            this.setState({
                menuDisplay: true
            })
        }else{
            this.setState({
                menuDisplay: false
            })
        }
    }


    render(){
        return(
            <div>
                <div className="menu" onClick= {this.onClickMenu}>
                    <span className="menu_line_1 menu_line"></span>
                    <span className="menu_line_2 menu_line"></span>
                    <span className="menu_line_3 menu_line"></span>
                    {this.state.menuDisplay === true ? <SideMenu /> : null}
                </div>
            </div>
        )
    }
}

export default Menu;